<template>
    <div class="settings-item-modal">
        <back-button :title="title || $route.name"
		@close="$router.go(-1)"/>
        <div class="settings-item-modal__body">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        title: String
    },
    mounted() {
        document.body.style.overflowY = "hidden";
    },
    destroyed() {
        document.body.style.overflowY = "auto";
    }
}
</script>

