import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  	screenHeight: 0,
  	isAuthenticated: true
  },
  mutations: {
  	UPDATE_SCREEN_HEIGHT(state, height) {
	    state.screenHeight = height
	  }
  },
  actions: {
  	updateScreenHeight({ commit }, height) {
      commit('UPDATE_SCREEN_HEIGHT', height)
    },
  },
  modules: {
  },
  getters: {
  	screenHeight: state => {
  		return state.screenHeight;
  	},
  	isAuthenticated: state => {
  		return state.isAuthenticated;
  	}
  }
})
