<template>
	<div style="padding-top: 40px;">
    <back-button 
    title="Reset password"
    @close="closeResetPassword"/>

	  <div v-if="!isEmailSended">
      <custom-input type="email" v-model="email" @enter="sendResetPasswordLetter">Email</custom-input>
      <custom-button 
          class="primary"
          :class="{'disabled' : !isValidEmail}"
          @click="sendResetPasswordLetter">Reset password
      </custom-button>
    </div>
    <div v-else class="thank-you d-flex flex-direction-column align-items-center text-center">
        <img src="/img/starter_icon5.svg">
        <div class="thank-you__text">
          <p class="standard-text">Email sent!</p>
        <p class="small-text">Click the link in the email that we just sent you to reset your password</p>
      </div>
      <custom-button class="primary">Open mail app</custom-button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'ResetPassword',
  components: {

  },

  data() {
  	return {
  		email: '',
      isEmailSended: false
  	}
  },
  computed: {
    isValidEmail() {
      var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(this.email);
    }
  },
  created() {
  	
  },
  methods: {
  	sendResetPasswordLetter() {
      this.isEmailSended = true;
    },
    closeResetPassword() {
      this.isEmailSended = false;
      this.$emit('close');
    }
  }
}
</script>
<style lang="scss" scoped="">

</style>
