<template>
    <div>
    	<div v-show="!isOpenResetPassword && !isLogined" :style="`min-height: ${__SCREEN_HEIGHT__ - 20}px`" class="d-flex flex-direction-column justify-content-between">
	    	<div class="text-center">
	    		<p class="middle-text">
	    			Your team:
	    		</p>
	    		<img src="/img/gipopotam.svg">
	    		<p class="standard-text pink">Pink gipopotam</p>
	    	</div>
	    	<div>
	    		<p class="standard-text m-0">Join the random team!</p>
	    		<p class="small-text m-0">
	    			Introduce yourself!
	    		</p>
	    		<form>
	    			<custom-input v-model="name">Nickname</custom-input>
	    			<custom-input type="password" v-model="password">password</custom-input>
	    			<custom-button 
	    			class="primary"
	    			:class="{'disabled' : !isValidFormData}"
	    			@click="login">Confirm</custom-button>
	    			<custom-button @click="resetPassword">Forgot your password?</custom-button>
	    		</form>
	    	</div>
    	</div>
    	<reset-password v-show="isOpenResetPassword" v-on:close="isOpenResetPassword = false"></reset-password>
    	<div v-if="isLogined" class="thank-you d-flex flex-direction-column align-items-center justify-content-center text-center" :style="`min-height: ${__SCREEN_HEIGHT__ - 20}px`">
    			<img src="/img/starter_icon5.svg">
    			<div class="thank-you__text">
	    			<p class="standard-text">Congratulations! <br> You get 50 coins.</p>
					<p class="small-text">Go to the achievement board and discover what you could get else!</p>
				</div>
				<custom-button class="primary" @click="showMainPage">Start</custom-button>
    	</div>
    </div>
</template>

<script>
import ResetPassword from './ResetPassword'

export default {
	components: {
		ResetPassword
	},
	data() {
		return {
			name: '',
			password: '',
			isOpenResetPassword: false,
			isLogined: false
		}
	},
	computed: {
		isValidFormData() {
			return this.name.length > 5 && this.password.length > 5
		}
	},
	methods: {
		resetPassword() {
			this.isOpenResetPassword = true;			
		},
		login() {
			if (this.isValidFormData === true) {
				this.isLogined = true;
			}
		},
		showMainPage() {
			this.$router.push({name: "Leaders"})
		}
	}
}
</script>
<style lang="scss" scoped>
	.pink {
		color: #DE3190;
	}
	.thank-you__text {
		width: 260px;
		margin-bottom: 2em;
	}
</style>
