<template>
   <div class="input-group">
		<input 
			class="inputMaterial"
			:class="{'focused' : value.length > 0}"
			@keydown.enter.prevent="$emit('enter')"
			:type="type" 
			v-model="value"
			v-on:input="updateValue(value)">
		<label>
			<slot/>
		</label>
	</div>
</template>

<script>
export default {
	props: {
		type: {
			type: String,
			default: 'text'
		}
	},
	data() {
		return {
			value: ''
		}
	},
	methods: {
		updateValue: function(value) {
			this.$emit('input', value)
		}
	}
}
</script>
<style lang="scss" scoped>
	.input-group {
		position: relative;
		background: #F6F9FC;
		box-shadow: inset 0px -1px 0px #394452;
		margin: 15px 0;
	}
	.inputMaterial {
	    font-size: 14px;
	    line-height: 18px;
	    padding: 10px 10px 5px 0px;
	    display: block;
	    height: 48px;
	    width: 100%;
	    border: none;
	    border-bottom: 1px solid #394452;
	    background-color: transparent;
	}
	label {
		color: #32325D;
	    font-size: 14px;
	    font-weight: normal;
	    position: absolute;
	    pointer-events: none;
	    left: 0px;
	    top: 15px;
	    transition: 0.2s ease all;
	    -moz-transition: 0.2s ease all;
	    -webkit-transition: 0.2s ease all;
	}
	.inputMaterial:focus {
		outline: none;
	}
	.inputMaterial:focus ~ label, 
	.inputMaterial.focused ~ label
	{
	    top: 0px;
	    font-size: 12px;
	    color: #5F7087;
	}
</style>
