<template>
	<div>
	<div class="fixed--heading">
      <a @click.prevent="openStories()" href="" class="fixed--heading__button fixed--heading__button_right d-inline-flex align-items-center">
        <span class="icon-eye"></span>
        <span>Stories</span>
      </a>
      <p class="middle-text text-center m-0">Tasks</p>
    </div>
	  <h1 class="standard-text">Time to change!</h1>
	  <p class="small-text">Personal development course. Complete daily tasks and keep moving forward.</p>
	  <template v-for="(task, index) in tasks">
	  		<div class="task--item" 
				
				 :class="'task--item_' + setItemRandomBackground()" 
				 @click="openTask(index)"
			>
			<div class="task--item-inner"  v-touch-options="{touchClass: 'active', swipeTolerance: 200, touchHoldTolerance: 300}" 
				 v-touch:moving="movingHandler"
				 v-touch:end="endMovingHandler">
			  	<div class="task--item__heading d-flex justify-content-between align-items-center">
			  		<span class="task--item__count">Task {{index}}</span>
			  		<div class="task--item__users d-flex align-items-center">
			  			Done by: 
			  			<div class="user--item">
			  				<img src="/img/user-image.png">
			  			</div>
			  			<div class="user--item">
			  				<img src="https://scontent.fiev1-1.fna.fbcdn.net/v/t31.0-8/18156529_618099718389540_5331521887665218786_o.jpg?_nc_cat=101&_nc_sid=cdbe9c&_nc_ohc=d4rlDfwLHBUAX-ZGYS2&_nc_ht=scontent.fiev1-1.fna&oh=e3aabc5b3fff3eb38e284e96f6ea7670&oe=5EBC4A83">
			  			</div>
			  			<a href="#">
			  				<img src="/img/dots.svg">
			  			</a>
			  		</div>
			  	</div>
			  	<div class="task--item__body">
			  		<p class="task--item__title">{{task.title}}</p>
			  		<span>{{task.text}}</span>
			  	</div>
			</div>
			<div class="task--item-hidden d-flex justify-content-center align-items-center">
				<a href="#" @click.stop="endTask()">
					<img src="/img/plus-icon.svg">
				</a>
			</div>
		  </div>
	  </template>
    </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
const colorsClasses = ['blue', 'purple', 'green', 'red'];
export default {
  name: 'LoginHome',
  components: {
    
  },
 
  data() {
  	return {
  		cursorPosition: null,
  		tasks: [
  			{
  				title: 'Wake Up at 6 a.m.',
  				text: 'Use an old-school alarm clock. Uninstall non-essential apps and social media'
  			},
  			{
  				title: 'Do a Workout for 20 min',
  				text: 'Do something light, like an easy/beginner yoga class or put on music and dance!'
  			},
  			{
  				title: 'Wake Up at 6 a.m.',
  				text: 'Use an old-school alarm clock. Uninstall non-essential apps and social media'
  			},
  			{
  				title: 'Do a Workout for 20 min',
  				text: 'Do something light, like an easy/beginner yoga class or put on music and dance!'
  			},
  			{
  				title: 'Wake Up at 6 a.m.',
  				text: 'Use an old-school alarm clock. Uninstall non-essential apps and social media'
  			},
  			{
  				title: 'Do a Workout for 20 min',
  				text: 'Do something light, like an easy/beginner yoga class or put on music and dance!'
  			}
  		]
  		
  	}
  },
  created() {
  	
  },
  methods: {
  	setItemRandomBackground() {
  		let randomNumber = Math.floor(Math.random() * Math.floor(colorsClasses.length));
  		return colorsClasses[randomNumber];
  	},
  	//переделать
  	movingHandler(e) {
  		e.stopPropagation();
  		
  		if(this.cursorPosition === null) {
  			this.cursorPosition = e.touches[0].clientX;	
  		}

  		if ((e.touches[0].clientX - this.cursorPosition) > -151 && 
  			(e.touches[0].clientX - this.cursorPosition) < 0) 
  		{
  			e.currentTarget.style.transform = `translateX(${e.touches[0].clientX - this.cursorPosition}px)`;
  			e.currentTarget.nextSibling.style.width = 
  			Math.abs(e.touches[0].clientX - this.cursorPosition)+ 'px';
  		}

  		if ((e.touches[0].clientX - this.cursorPosition) < 0){
  			e.currentTarget.dataset.swipeTo = 'left';	
  			
  		} else {
  			e.currentTarget.dataset.swipeTo = 'right';
  		}
  		
  	},
  	endMovingHandler(e) {
  		
  		if (e.currentTarget.dataset.swipeTo == 'left') {  			
  			e.currentTarget.style.transition = 'all .4s';
  			e.currentTarget.style.transform = 'translateX(-130px)';
  			e.currentTarget.nextSibling.style.width = '150px';
  			e.currentTarget.classList.add('open');

  		} else {
  			e.currentTarget.style.transform = 'translateX(0px)';
  			e.currentTarget.nextSibling.style.width = '0px';
  			e.currentTarget.classList.remove('open');
  		}
  		e.currentTarget.style.transition = '';
  		this.cursorPosition = null;
  	},
  	openTask(id) {
  		this.$router.push({ name: 'Task', params: { id: id } })
  	},
  	openStories() {
  		this.$router.push({ name: 'Stories'})	
  	},
  	endTask(e) {
  		confirm('Complete task?');
  	}
  }
}
</script>
<style lang="scss" scoped="">
	.standard-text {
		margin-bottom: 0;
	}
	.small-text {
		margin-top: .5em;
	}

	.task--item-inner {
		background-color: transparent;
	}
	
</style>
