import Vue from 'vue'
import VueRouter from 'vue-router'
import NavbarLayout from '@/layout/NavbarLayout.vue'
import Home from '../views/Home.vue'
import LoginHome from '@/views/login/LoginHome.vue'
import Leaders from '@/views/leaders/Leaders.vue'
import TaskList from '@/views/tasks/TaskList.vue'
import Task from '@/views/tasks/Task.vue'
import Chat from '@/views/Chat.vue'
// profile pages
import Profile from '@/views/profile/Profile.vue'
import ProfileMail from '@/views/profile/partials/Mail.vue'
import ProfileDeactivateAccount from '@/views/profile/partials/DeactivateAccount.vue'
// settings pages
import Settings from '@/views/settings/Settings.vue'
import store from '@/store/index'
Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: LoginHome
  },
  {
    path: "/",
    component: NavbarLayout,
    redirect: { name: 'Login' },
    children: [
      {
        path: 'leaders',
        name: 'Leaders',
        component: Leaders
      },
      {
        path: 'tasks',
        name: 'Tasks',
        component: TaskList,
      },
      {
        path: 'tasks/:id',
        name: 'Task',
        component: Task
      },
      {
        path: 'stories',
        name: 'Stories',
        component: () => import(/* webpackChunkName: "stories" */ '../views/tasks/Stories.vue')
      },
      {
        path: 'chat',
        name: 'Chat',
        component: Chat
      },
      {
        path: 'profile',
        name: 'Profile',
        component: Profile,
        children: [
          {
            path: 'mail',
            name: 'profile.mail',
            component: ProfileMail
          },
          {
            path: 'deactivate-account',
            name: 'profile.deactivate-account',
            component: ProfileDeactivateAccount
          },
        ]
      },
    ]
  },
  {
    path: '/settings',
    name: 'Settings',
    component: Settings
  }
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  
  if (to.name && to.name.toLowerCase() != 'login' && store.getters.isAuthenticated == false) {
    router.push({ path: '/login'  })
  }
  
  return next()
});

export default router
